.AutoCompleteText{
    border: 1px solid gray;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .1), 0 2px 4px 1px rgba(0, 0, 0, .18);
    font-size: 18px;
}

.AutoCompleteText input{
    width: 100%;
    border: none;
    font-size: 18px;
    padding: 10px 5px;
    box-sizing: border-box;
    outline: none;
}

.AutoCompleteText ul{
    list-style-type: none;
    text-align: left;
    margin: 0;
    margin-top: -2px;
    padding: 0;
    border-top: 1px solid gray;
}

.AutoCompleteText ul::before{
    content: "";
}

.AutoCompleteText li{
    padding: 10px 5px;
    cursor: pointer;
}

.AutoCompleteText li:hover {
    background: gray;
}